.menu{
  height: 900px;
}

.menu-english-img{
  background-image: url(menu/menuEnglish.jpg);
    width: 100%;
    height: auto;
    background-repeat: no-repeat;
    background-size: contain;
}

.menu-chinese-img{
  background-image: url(menu/menuChinese.jpg);
    width: 100%;
    height: auto;
    background-repeat: no-repeat;
    background-size: contain;
}

.status {
  margin-bottom: 10px;
}

/*Nav Start*/

.logo-icon{
  background-image: url(desgin/img/trizia-logo.svg);
  background-repeat: no-repeat;
  height: 100px;
  width: 100px;
}

body{
  margin: 0;
  font-family: 'Oswald', sans-serif;
  font-weight: 400;
  font-size: 20px;
}
header{
}

header::after {
  content: '';
  display: table;
  clear: both;
}

.container-nav{
  width: 95%;
  margin: 0 auto;
}

.logo{
  float: left;
  margin-top: 5px;
  width: 100px;
  height: 100px;
}

.container-nav nav{
  float: right;
}

.container-nav nav ul{
  margin: 0;
  padding: 0;
  list-style: none;
}

.container-nav nav li{
  display: inline-block;
  margin-left: 25px;
  padding-top: 35px;
  position: relative;
}

.container-nav nav a {
  color: #a56246;
  font-size: 24px;
  text-decoration: none;
}

.container-nav nav a:hover {
  color: #431717;
  text-decoration: none;
}

.container-nav nav a:hover::before{
  position: absolute;
  content: '';
  display: block;
  height: 5px;
  width: 100%;
  background-color: #a56246;
  top: 0;
}

.container-mv-nav{
  margin: 0 auto;
}

.container-mv-nav nav{
  clear: both;
  padding-bottom: 12px;
}

.container-mv-nav nav ul{
  margin: 0;
  padding: 0;
  list-style: none;
}

.container-mv-nav nav li{
  text-align: center;
  position: relative;
}


.container-mv-nav nav a{
  color: #a56246;
  font-size: 20px;
  text-decoration: none;
}

.container-mv-nav nav a:hover {
  color: #431717;
  text-decoration: none;
}
.float-right{
  float: right;
}

.hamburger-icon{
  background-image: url(desgin/img/menu-icon.svg);
  background-repeat: no-repeat;
  height: 28px;
  width: 28px;
}

.hamburger-icon-white{
  background-image: url(desgin/img/menu-icon-white.svg);
  background-repeat: no-repeat;
  height: 28px;
  width: 28px;
}

.mrg-16{
  margin:16px;
}

.mrg-48{
  margin: 48px 15px 48px 0px;
}

.logo-icon-50{
  background-image: url(desgin/img/trizia-logo-50.svg);
  background-repeat: no-repeat;
  height: 50px;
  width: 50px;
}

.logo-50{
  float: left;
  width: 50px;
  height: 50px;
}

.fill-color-white{
  background-color: #fff;
}
.fill-color-brown{
  background-color: #a56246;
}



@media only screen and (max-width: 465px) {
    .hide-dt-view{
      display: none;
    }
}

@media only screen and (min-width: 464px) {
    .hide-mv-view{
      display: none;
    }
}


.display-block{
  display: block;
}

.display-none{
  display: none;
}

.btn-color{
  background-color: #a56246 !important;
  border-color: #a56246 !important;
}

.pad-10{
  padding: 10px;
}
/*Nav End*/



